import React,  { useState }  from "react";

function Nombre(props){
    const { nombre, ChangeName } = props;

    const [jugar, setJugar] = useState('');

    const ChangeNombre = (e) => {
        ChangeName(e.target.value);

    };

    return(
        <label>Jugador/a:
             <input id="nombre" type="text" placeholder="INGRESE SU NOMBRE" value={nombre} onChange={ChangeNombre}></input>
             <button type="submit" id="jugar" onClick={() => setJugar(true)}>JUGAR</button>
        </label>


        
    );
}     
export default Nombre;